import React, {Component} from 'react';
import './style.css';
import NewsItem from './../NewsItem/index';
import placeholderPic from './news-placeholder.png';

class NewsWrapper extends Component {
	constructor(props) {
		super(props);

		this.state = {
			idCounter: 3,
			feedURLS: [
				this.setFeedUrl`educationnews`,
				this.setFeedUrl`earth`
			],
			readLaterList: JSON.parse(localStorage.getItem('readLaterArr')) || [],
			readLaterMode: this.props.readLaterMode
		};
	}

	setFeedUrl = (category) => {
		return `https://api.rss2json.com/v1/api.json?rss_url=https%3A%2F%2Fwww.nasa.gov%2Frss%2Fdyn%2F${category}.rss`;
	};

	getNewsFeed = () => {
		const urls = this.state.feedURLS;
		let newsArr = [];
		let newsArrFiltered;

		Promise.all(urls.map(url =>
			fetch(url).then(resp => resp.json())
		)).then(data => {
			for (let feedItem of data) {
				newsArr.push(...feedItem.items);
			}

			newsArrFiltered = newsArr.map((item) => {
				if (item.title.indexOf('&') === -1) {
					return item;
				}
			}).filter(item => item !== undefined);

			this.setState({
				newsItems: [...newsArrFiltered]
			});
		}).catch(error => {
			console.log('Error!', error);
		});
	};

	componentDidMount() {
		this.getNewsFeed();
	}

	showMoreNews = (ev) => {
		const freshNewsQuantity = 3;
		const {idCounter, newsItems} = this.state;
		const target = ev.target;

		if (idCounter === newsItems.length) {
			this.setState({
				idCounter: 3
			});
			target.innerHTML = 'Show more';
			return;
		} else if (idCounter + freshNewsQuantity >= newsItems.length) {
			target.innerHTML = 'Hide';
		}

		this.setState(prevState =>
			({
				idCounter: (() => {
					return (prevState.idCounter + freshNewsQuantity <= prevState.newsItems.length) ? prevState.idCounter + freshNewsQuantity : prevState.newsItems.length;
				})()
			})
		);
	};

	refreshNews = () => {
		this.getNewsFeed();
	};

	checkReadLaterQuantity = () => {
		const counter = this.state.readLaterList.length;
		const readLaterCounter = document.querySelector('.App__time-icon-counter');

		readLaterCounter.innerHTML = counter;
	};

	addToReadLater = (itemWrapper) => {
		const itemId = itemWrapper.dataset.key;
		const itemsArr = this.state.readLaterList;
		const itemInfoWrapper = itemWrapper.querySelector('.NewsItem--full-info');
		const itemTitle = itemInfoWrapper.querySelector('.NewsItem-title').textContent;
		const itemDescr = itemInfoWrapper.querySelector('.NewsItem-description').textContent;
		const itemPic = itemInfoWrapper.querySelector('.NewsItem-pic').getAttribute('src');

		if (itemWrapper.classList.contains('is-active')) {
			localStorage.setItem(
				'readLaterArr',
				JSON.stringify([...itemsArr,
					{
						itemId: itemId,
						itemTitle: itemTitle,
						itemDescription: itemDescr,
						itemPic: itemPic
					}
				]));

			this.setState({
				readLaterList: JSON.parse(localStorage.getItem('readLaterArr'))
			});
		} else {
			for (let item of itemsArr) {
				if (item.itemId === itemId) {
					const arrItemId = itemsArr.indexOf(item);

					itemsArr.splice(arrItemId, 1);
					localStorage.setItem('readLaterArr', JSON.stringify(itemsArr));

					this.setState({
						readLaterList: itemsArr
					})
				}
			}
		}

		this.checkReadLaterQuantity();
	};

	render () {
		const {newsItems, idCounter, readLaterList: itemsArr, readLaterMode} = this.state;
		let newsItemsList;
		const searchQuery = this.props.searchQuery;
		const title = document.createElement('span');
		title.classList.add('NewsWrapper__title');
		title.innerHTML = 'Nothing found :(';
		const appTitle = document.querySelector('.NewsWrapper__title');
		const newsButtonsWrapper = document.querySelector('.NewsWrapper__buttons-wrapper');
		let newsButtonsWrapperClasses;

		if (newsButtonsWrapper !== null) {
			newsButtonsWrapperClasses = newsButtonsWrapper.classList;
		}

		if (newsItems !== undefined) {
			if (readLaterMode) {
				newsItemsList = itemsArr.map((item, i) => {
					if (searchQuery === '') {
						if (i < idCounter) {
							return <NewsItem picPlaceholder={placeholderPic} pic={item.itemPic} title={item.itemTitle}
							                 description={item.itemDescription} key={i} keyVal={i}
							                 checkReadLaterQuantity={this.checkReadLaterQuantity}
							                 addToReadLater={this.addToReadLater} itemReadLaterStatus={true} />;
						}

						if (newsButtonsWrapperClasses.contains('hidden')) {
							newsButtonsWrapperClasses.remove('hidden');
						}
					} else {
						if (item.itemTitle.toLowerCase().includes(searchQuery.toLowerCase()) || item.itemDescription.toLowerCase().includes(searchQuery.toLowerCase())) {
							return <NewsItem picPlaceholder={placeholderPic} pic={item.itemPic} title={item.itemTitle}
							                 description={item.itemDescription} key={i} keyVal={i}
							                 checkReadLaterQuantity={this.checkReadLaterQuantity}
							                 addToReadLater={this.addToReadLater} itemReadLaterStatus={true} />;
						}

						if (!newsButtonsWrapperClasses.contains('hidden')) {
							newsButtonsWrapperClasses.add('hidden');
						}
					}
				});
			} else {
				newsItemsList = newsItems.map((item, i) => {
					const title = item.title;
					const description = item.description;
					const pic = item.enclosure.link;
					const pubDate = item.pubDate.replace(/(-| |:)/g, '');
					const itemReadLaterStatus = itemsArr.some(function (item) {
						return item.itemId === pubDate;
					});

					if (searchQuery === '') {
						if (i < idCounter) {
							return <NewsItem picPlaceholder={placeholderPic} pic={pic} title={title}
							                 description={description} key={pubDate} keyVal={pubDate}
							                 checkReadLaterQuantity={this.checkReadLaterQuantity}
							                 addToReadLater={this.addToReadLater} itemReadLaterStatus={itemReadLaterStatus} />;
						}

						if (newsButtonsWrapperClasses.contains('hidden')) {
							newsButtonsWrapperClasses.remove('hidden');
						}
					} else {
						if (title.toLowerCase().includes(searchQuery.toLowerCase()) || description.toLowerCase().includes(searchQuery.toLowerCase())) {
							return <NewsItem picPlaceholder={placeholderPic} pic={pic} title={title}
							                 description={description} key={pubDate} keyVal={pubDate}
							                 checkReadLaterQuantity={this.checkReadLaterQuantity}
							                 addToReadLater={this.addToReadLater} itemReadLaterStatus={itemReadLaterStatus} />;
						}

						if (!newsButtonsWrapperClasses.contains('hidden')) {
							newsButtonsWrapperClasses.add('hidden');
						}
					}
				});
			}

			if (newsItemsList.every(item => item === undefined)) {
				console.log(newsItemsList);
				if (appTitle === null) {
					document.querySelector('.NewsWrapper').append(title);
				}

				if (!newsButtonsWrapperClasses.contains('hidden')) {
					newsButtonsWrapperClasses.add('hidden');
				}
			} else {
				if (appTitle !== null) {
					appTitle.parentNode.removeChild(appTitle);
				}
			}
		}

		return (
			<div className="NewsWrapper">
				{newsItemsList}
				<div className="NewsWrapper__buttons-wrapper">
					<div className="button button--pink NewsWrapper__button NewsWrapper__button--show-more" onClick={this.showMoreNews}>Show more</div>
					<div className="button button--pink NewsWrapper__button" onClick={this.refreshNews}>Refresh news</div>
				</div>
			</div>
		);
	}
}

export default NewsWrapper;


