import React, {Component} from 'react';
import timeIcon from '../ui-kit/wall-clock.svg';
import timeIconActive from '../ui-kit/wall-clock-active.svg';
import PreloaderIcon, {ICON_TYPE} from 'react-preloader-icon';
import './style.css';

class NewsItem extends Component {
	state = {
		readLaterStatus: this.props.itemReadLaterStatus,
		loadedComponent: false
	};

	showFullNewsItem = (ev) => {
		const target = ev.target;

		if (!target.closest('.NewsItem__time-icon')) {
			const newsItemFullInfo = document.querySelector('.NewsFullItem');
			const newsItemNewInfo = target.closest('.NewsItem').querySelector('.NewsItem--full-info').cloneNode(true);
			const newsItemOldInfo = newsItemFullInfo.querySelector('.NewsItem--full-info');
			const newsItemShadow = document.querySelector('.shadow-block');
			const body = document.body;
			const html = body.parentElement;
			const htmlStyles = {
				overflow: 'hidden',
			};

			if (body.offsetHeight > html.clientHeight) {
				htmlStyles.marginRight = '8px';
			}

			if (newsItemOldInfo === null) {
				newsItemFullInfo.appendChild(newsItemNewInfo);
			} else {
				newsItemFullInfo.removeChild(newsItemOldInfo);
				newsItemFullInfo.appendChild(newsItemNewInfo);
			}
			newsItemFullInfo.classList.add('is-active');
			newsItemShadow.classList.add('is-active');
			body.style.overflow = 'hidden';
			for (let key in htmlStyles) {
				html.style[key] = htmlStyles[key];
			}
		}
	};

	addToReadLater = (ev) => {
		const target = ev.target;
		const itemWrapper = target.closest('.NewsItem');

		itemWrapper.classList.toggle('is-active');

		this.setState(itemWrapper.classList.contains('is-active') ? {
			readLaterStatus: true
		} : {
			readLaterStatus: false
		});

		setTimeout(() => {
			this.props.addToReadLater(itemWrapper);
		}, 0);
	};

	componentDidMount () {
		setTimeout(() => {
			this.setState({
				loadedComponent: !this.state.loadedComponent
			});

			this.props.checkReadLaterQuantity();
		}, 400);
	}

	render() {
		const {title, description, pic, picPlaceholder} = this.props;
		let newsTitle = title.trim();
		let newsDescr = description.trim();
		const maxTitleLength = 60;
		const maxDescrLength = 135;
		const readLaterStatus = this.state.readLaterStatus;

		if (newsTitle.length && newsTitle.length > maxTitleLength) {
			newsTitle = newsTitle.slice(0, maxTitleLength) + '...';
		}

		if (newsDescr.length && newsDescr.length > maxDescrLength) {
			newsDescr = newsDescr.slice(0, maxDescrLength) + '...';
		}

		return (
			<div className={`NewsItem ${readLaterStatus ? 'is-active' : ''}`} onClick={this.showFullNewsItem} data-key={this.props.keyVal}>
				{
					this.state.loadedComponent ?
						<div className="NewsItem-pic" style={{backgroundImage: `url(${pic})` || `url(${picPlaceholder}`}}>
							<img src={pic || picPlaceholder} alt="" />
						</div>
					: (
					<PreloaderIcon
						type={ICON_TYPE.PUFF}
						size={100}
						strokeWidth={8}
						strokeColor="#e07685"
						duration={900}
						style={{padding: '40%', height: '100%', width: '100%'}}
					/>
				)}
				<div className={`NewsItem__time-icon ${readLaterStatus ? 'is-active' : ''}`}>
					<img src={readLaterStatus ? timeIconActive : timeIcon} alt="Read later" onClick={this.addToReadLater} />
					<span className="tooltip">{readLaterStatus ? `Remove from "Read later" list` : `Add to "Read later" list`}</span>
				</div>
				<div className="NewsItem-wrapper">
					<h3 className="NewsItem-title">{newsTitle}</h3>
					<span className="NewsItem-description">{newsDescr}</span>
					<div className="NewsItem__arrow" />
				</div>
				<div className="NewsItem--full-info">
					<img src={pic || picPlaceholder} alt="" className="NewsItem-pic" />
					<h3 className="NewsItem-title">{title}</h3>
					<span className="NewsItem-description">{description}</span>
				</div>
			</div>
		);
	}
}

export default NewsItem;


