import React, {Component} from 'react';
import '../ui-kit/button.css';
import '../ui-kit/close-btn.css';
import '../ui-kit/input.css';
import '../ui-kit/tooltip.css';
import arrowUp from '../ui-kit/up-arrow.svg';
import closeIcon from '../ui-kit/close-icon.svg';
import timeIconActive from '../ui-kit/wall-clock-active.svg';
import '../NewsFullItem.css';
import './style.css';
import NewsWrapper from '../NewsWrapper/index';
import {BrowserRouter as Router, Route, Switch, Link} from 'react-router-dom';

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			searchQuery: ''
		}
	}

	componentDidMount() {
		window.addEventListener('scroll', function () {
			const appHeader = document.querySelector('.App-header');
			const appHeaderClassList = appHeader.classList;
			const arrowUp = document.querySelector('.App__arrow-up');

			if (this.pageYOffset > '30') {
				if (!appHeaderClassList.contains('is-active')) {
					appHeaderClassList.add('is-active');
				}
				!arrowUp.classList.contains('visible') ? arrowUp.classList.add('visible') : null;
			} else {
				if (appHeaderClassList.contains('is-active')) {
					appHeaderClassList.remove('is-active');
				}
				arrowUp.classList.contains('visible') ? arrowUp.classList.remove('visible') : null;
			}
		});
	}

	scrollUp = () => {
		let scrollTimeout;
		(function scrollToTop () {
			if (window.pageYOffset !== 0) {
				scrollTimeout = setTimeout(() => {
					window.scrollTo(0, window.pageYOffset - 30);
					scrollToTop();
				}, 10);
			} else {
				clearTimeout(scrollTimeout);
			}
		})();
	};

	searchNews = (ev) => {
		const target = ev.target;
		const appCloseBtn = target.closest('.App-search').querySelector('.close-btn');

		this.setState({
			searchQuery: target.value
		});

		if (target.value !== '') {
			if (!appCloseBtn.classList.contains('is-active')) {
				appCloseBtn.classList.add('is-active');
			}
		} else {
			if (appCloseBtn.classList.contains('is-active')) {
				appCloseBtn.classList.remove('is-active');
			}
		}
	};

	hideInfoBlock = () => {
		const newsItemWrapper = document.querySelector('.NewsFullItem');
		const shadowBlock = document.querySelector('.shadow-block');
		const body = document.body;
		const html = body.parentElement;
		const htmlStyles = {
			overflow: 'visible',
		};

		if (body.offsetHeight > html.clientHeight) {
			htmlStyles.marginRight = '0';
		}

		if (newsItemWrapper.classList.contains('is-active')) {
			newsItemWrapper.classList.remove('is-active');
			shadowBlock.classList.remove('is-active');
			body.style.overflow = 'auto';
			for (let key in htmlStyles) {
				html.style[key] = htmlStyles[key];
			}
		}
	};

	clearSearchInput = () => {
		const searchInput = document.querySelector('.App-search__input');
		const inputEv = new Event('change');

		searchInput.addEventListener('change', this.searchNews);
		searchInput.value = '';
		searchInput.dispatchEvent(inputEv);
	};

	render() {
		const {searchQuery} = this.state;

		window.searchQuery = searchQuery;
		
		return (
			<div className="App">
				<Router>
					<div>
						<div className="App-header--wrapper">
							<div className="App-header">
								<Link className="App-title sword-effect" to={
									{
										pathname: '/grownews'
									}
								}>
									Grow News
								</Link>
								<Link className="App__time-icon" to={
									{
										pathname: '/grownews/read-later'
									}
								}>
									<img src={timeIconActive} alt="Read later" />
									<span className="App__time-icon-counter" />
								</Link>
							</div>
						</div>
						<div className="App-search">
							<input type="text" placeholder="Search news..." className="App-search__input" onInput={this.searchNews} />
							<img src={closeIcon} className="close-btn App-search__close-btn" alt="" onClick={this.clearSearchInput} />
						</div>

						<Switch>
							<Route exact path={'/'} component={NewsWrapperList} />
							<Route path={'/read-later'} component={ReadLaterList} />
						</Switch>

						<img src={arrowUp} alt="" className="App__arrow-up" onClick={this.scrollUp} />
						<div className="NewsFullItem">
							<img src={closeIcon} className="close-btn" alt="" onClick={this.hideInfoBlock} />
						</div>
						<div className="shadow-block" onClick={this.hideInfoBlock} />
					</div>
				</Router>
			</div>
		);
	}
}

export default App;

class NewsWrapperList extends Component {
	render () {
		return (
			<NewsWrapper searchQuery={window.searchQuery} readLaterMode={false} />
		);
	}
}

class ReadLaterList extends Component {
	render () {
		return (
			<div>
				<div className="App-title NewsWrapper-title">Read later list</div>
				<NewsWrapper searchQuery={window.searchQuery} readLaterMode={true} />
			</div>
		);
	}
}



